}

.button.accent:hover {
  --primary: #f97031;
  --secondary: #f1ebe2;
}

.button[data-disabled="true"] {
  --primary: lightgrey;
  --secondary: gray;
  cursor: not-allowed;
}

.button[data-disabled="true"]:hover {
  --primary: lightgray;
  --secondary: gray;
}

.button.flat {
  background-color: var(--primary);
  border-color: var(--primary);
  color: var(--secondary);
  box-shadow: 0 .5rem 1rem #0000001a;
}

.button.outline {
  border-color: var(--primary);
  color: var(--primary);
  background-color: #0000;
}

.button.text {
  color: var(--primary);
  background-color: #0000;
