}

.country-data[data-titles="true"] {
  width: 0;
  white-space: nowrap;
  background-color: #0000;
  align-items: flex-end;
  overflow: visible;
}

.country-data[data-titles="false"] {
  text-align: center;
  vertical-align: middle;
  background-color: #fff;
  flex: 1 0;
  font-size: .85rem;
  overflow: hidden;
}

.country-data[data-titles="false"] * {
  line-height: 2rem;
}

.country-data > * {
  min-height: 3rem;
  padding: .5rem 1em;
}

.country-data > :nth-child(2n+1) {
  background-color: #0000001a;
}

.country-data * {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
