  border-radius: 0;
}

.country-data .percentiles, .country-data .salary {
  flex: row nowrap;
  justify-content: stretch;
  display: flex;
  position: relative;
}

.country-data .percentiles > div, .country-data .salary > div {
  flex: 1px;
  position: relative;
}

.country-data .percentiles > div:not(:last-child):after, .country-data .salary > div:not(:last-child):after {
  content: "";
  width: .5px;
  height: 100%;
  opacity: .1;
  background-color: #000;
  border-radius: 1rem;
  position: absolute;
  top: 0;
  right: 0;
}

.country-data .salary:not(:last-child):after {
  content: "";
  width: 100%;
  height: .5px;
  opacity: .1;
  background-color: #000;
  border-radius: 1rem;
  position: absolute;
  bottom: 0;
}

.country-data-options {
  justify-content: space-between;
  gap: 1em;
  display: flex;
}

.country-data-options select {
  max-width: 150px;
  flex: 1 0 auto;
}

.country-data:first-child {
  border-top-left-radius: .5em;
  border-bottom-left-radius: .5em;
}

.country-data:first-child > * {
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
}

.country-data:not(:last-child):after {
  content: "";
  height: calc(100% - 7rem);
  width: 1px;
  background-color: #0000001a;
  border-radius: 1rem;
  position: absolute;
  top: calc(3rem + 50%);
  right: 0;
  transform: translateY(-50%);
}

.country-data:last-child {
  border-top-right-radius: .5em;
  border-bottom-right-radius: .5em;
}

.country-data:last-child > * {
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

.modalcontainer {
  visibility: hidden;
  opacity: 0;
  display: none;
}

.modalcontainer.active {
  visibility: visible;
  opacity: 1;
  display: block;
}

.modalcontainer .modal, .modalcontainer .backlayer {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modalcontainer .modal {
  z-index: 10000;
  height: calc(100% - 4em);
  width: calc(100% - 4em);
  max-width: 480px;
  background: #fff;
  border-radius: 1rem;
  grid-template: "op op op" 1fr
                 "ok . xx" 3rem
                 / 1fr 1rem 6rem;
  padding: 1em;
  display: grid;
  overflow-x: hidden;
  overflow-y: auto;
}

.modalcontainer .backlayer {
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  background-color: #000000d9;
}

.modalcontainer .options {
  height: 100%;
  -ms-overflow-style: none;
  scrollbar-width: none;
  grid-area: op;
  overflow: auto;
}

.modalcontainer .options::-webkit-scrollbar {
  display: none;
}

.modalcontainer .button {
  width: 100%;
}

.modalcontainer .done {
  grid-area: ok;
  margin-right: 1rem;
}

.modalcontainer .close {
  grid-area: xx;
}

